<template>
  <v-content>
    <v-row style="margin:0" align="center" justify="center">
      <v-btn @click="openResetPass" block>Reset password</v-btn>
    </v-row>

    <v-dialog :value="resetPassword" persistent width="600">
      <v-sheet>
        <v-form ref="form" v-model="valid">
          <v-row justify="center" style="margin:0">
            <h1>Reset password</h1>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Old password</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="resetForm.old"
                :error-messages="wrongPass"
                :rules="[rules.required]"
                type="password"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">New password</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="resetForm.new"
                :rules="[rules.required]"
                type="password"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Confirm new password</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="resetForm.confirm"
                :rules="[rules.required, rules.muchConfirm]"
                type="password"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
            <v-btn @click="close" color="error" :normal="true">{{$t("addAndEditClient.close")}}</v-btn>
            <v-btn color="primary" @click="resetPass" :normal="true">{{$t("addAndEditClient.save")}}</v-btn>
          </v-row>
        </v-form>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import _ from "lodash";
export default {
  name: "resetPassword",
  data() {
    return {
      resetPassword: false,
      valid: false,
      resetForm: {
        old: "",
        new: "",
        confirm: ""
      },
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        email: v => /.+@.+\..+/.test(v) || "E-mail must be valid",
        muchConfirm: v =>
          this.test(v) || `Need to be the same with the new password`
      }
    };
  },
  computed: {
    ...mapGetters(["wrongPassForReset"]),
    wrongPass() {
      const errors = [];
      if (this.wrongPassForReset) {
        errors.push("The password is wrong");
        return errors;
      } else {
        return [];
      }
    }
  },
  created() {
    this.$store.commit("wrongPassForReset", false);
  },
  methods: {
    test() {
      if (this.resetForm.new == this.resetForm.confirm) {
        return true;
      } else {
        return false;
      }
    },
    openResetPass() {
      this.resetPassword = true;
    },
    close() {
      this.resetPassword = false;
    },
    async resetPass() {
      console.log(this.resetForm);
      if (this.$refs.form.validate()) {
        let data = _.cloneDeep(this.resetForm);
        delete data.confirm;

        await this.$store.dispatch("resetPass", data);
      }
    }
  }
};
</script>